import React from 'react'
import clsx from 'clsx'

const Day7 = ({ show }) => (
  <div className={clsx('day day--inverse', show && 'day--show')} style={{ backgroundColor: 'DarkRed' }}>
    <h1>День 7</h1>
    <h4>Колір цього дня - DarkRed.</h4>
    
    <p>Я дивлюсь Портникова і він каже, що щасливий жити зараз. Бо зараз все змінюється. Немає стабільності. Але є прогнози. Немає впевненості у завтрашньому дні. Але є вектор розвитку.</p>

    <p>Я теж радію, що я бачу зміни у світі. Вчора був на стендапі та нотував, наскільки люди не хочуть знати про світ. Кожен мешкає в своїй бульбашці та кожного це влаштовує. Але чому їх бульбашки такі нецікаві?</p>

    <p>Ще два роки тому я дивився тільки розважальні шоу, читав художню літературу та наукові статті. Серіали та фільми заповнювали мій простір, життя у порівнянні з кіно здавалось таким нудним.</p>

    <p>Зараз я і дня не можу уявити без новин, світ дивовижний. Історії повторюються як і в кінематографі чи літературі, але кожен раз вони такі неповторні. Нації воюють за свободу та самовизначення. Демократія та авторитаризм все ще б’ються і ми в центрі цієї боротьби.</p>

    <p>Я не знав, що свобода така важлива для людини. Коли я був у школі та писав твір на тему свободи, я, звісно, пхав туди усі ці клішовані фрази на штиб “невільна людина нещаслива”, або “кожен має бути незалежним від інших”. Фрази були, а усвідомлення ні.</p>

    <p>Я зрозумів наскільки ми в нашій країні вільні нещодавно. Коли обрав собі ідентичність та став часткою української політичної нації. Але в мене є один величезний комплекс. Я дуже шкодую, що я таким став тільки зараз.</p>

    <p>У 2004му ми з батьком та друзями ходили в помаранчевих футболках “Ющенко ТАК!”, роздавали газети, того дня Віктор Андрійович приїздив в наше селище з виступом та концертною програмою. Батько сказав, що киримли підтримують Ющенко, тому ми підтримували його. Чому саме так, батько не пояснив, я гадаю, що він і сам не знав, сказали й сказали, чого заморочуватись.</p>

    <p>Ющенко виступив, Білозір заспівала, а ми отримали по футболці та по 20 грн. Потім перший майдан, конституційний суд, перевибори. Розуміння чому все це відбувається досі не було, але те, що опонент нашого кандидата мразота, я усвідомлював.</p>

    <p>Коли був другий майдан, я навіть не цікавився ним. Мені було настільки байдуже, бо старики нещодавно померли, батьки розлучались, а нормальної роботи в мене не було. І я просто, як і більшість наших громадян, просто ненавидів усе, що підвищує курс долара. Як я зараз жалію, що тоді не був поруч з українцями та не бився з ними пліч-о-пліч.</p>

    <p>Winter on Fire посилив в мені цей комплекс. Я звинувачую себе тільки частково. Мені здається, що розуміння ситуації в країні та світі повинно вкладатись в тебе батьками. Мої доволі інертні, ніколи не рефлексують, а життя будували за лекалами совєцької людини. Невільні люди ніколи не готові до свободи. Тому їх я звинувачую не в тому, що вони не розуміли й не пояснювали мені світ, а в тому, що вони не дали мені орієнтири. Адже мій рідний народ сповнений великих особистостей, <a href="https://uk.wikipedia.org/wiki/%D0%9C%D1%83%D1%81%D1%82%D0%B0%D1%84%D0%B0_%D0%94%D0%B6%D0%B5%D0%BC%D1%96%D0%BB%D1%94%D0%B2" rel="noreferrer" target="_blank">найвеличніша</a> навіть зараз жива і мешкає зі мною в одній країні. От просто сказали б: “Дивись, Умере, це дуже крута людина, тому що вона у повній безнадійності воювала за правду та свободу, слухай її уважно”. Ех, якби все так і було… </p>

    <p style={{ textAlign: 'center' }}>***</p>

    <p>Я не розмовляв з батьком вже майже рік. Я дійсно хочу, але не знаю з чого почати. Мабуть, треба з ним обговорити новини та політику. Чи просто погоду.</p>

  </div>
)

export default Day7
