    <p>Темне небо як верхній елемент гідравлічного преса. Бетонна підлога як основа. Я іграшкова свинка Пепа, котру <a href="https://youtu.be/UGys_c5PHH0?t=202" target="_blank" rel="noreferrer">блогер в ютубі буде чавити</a>. Збере свої лайки, а у наступному відео замінить мене айфоном. Чи капцями. Чи іншою іграшкою.</p>

